.sideBar {
  height: 100vh;
  position: fixed;
  width: 328px;
  background: #fbfbfb;
}

.div_projects {
  width: 329px;
  height: 58px;
  top: 93px;
  border: none;
  display: block;
}

.icon {
  position: absolute;
  height: 19px;
  width: 20px;
  margin-left: -40px;
  margin-top: 2px;
}

.btn_projects {
  border: none;
  color: rgba(67, 63, 63, 1);
  padding-left: 90px;
  padding-top: 15px;
  cursor: pointer;
  height: 58px;
  text-decoration: none;
}
.btn_projects:hover {
  color: rgba(67, 63, 63, 1);
}



.log_out_icon {
  margin-top: 2px;
  position: absolute;
  width: 26px;
  height: 26px;
  margin-left: 10px;
}

.btn_logout {
  position: absolute;
  height: 36px;
  width: 100px;
  border: none;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 27px;
  background: #fbfbfb;
}


.logout {
  
  position: fixed;
    display: inline-block;
    color: #433f3f;
    margin-left: 96px;
    padding-bottom: -20px;
    bottom: 50px;
}
